import React, { useState } from "react";
import { Button, Modal } from "react-bootstrap";
import { HiExclamation } from "react-icons/hi";
import { FaStethoscope } from "react-icons/fa";
import { IoIosArrowRoundBack } from "react-icons/io";
import VideoCall from "./VideoCall";
import { getVonageVideoCallToken } from "../../../../app/features/vonageVideoCall/vonageVideoCall.slice";
import { useDispatch, useSelector } from "react-redux";
import { joinAppointment } from "../../../../app/features/appointment/appointment.slice";
import { ClipLoader } from "react-spinners";

export default function VideoAppointment() {
  const slotDescription = JSON.parse(localStorage.getItem('slotDescription'));

  // const { slotDescription } = props;
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const [startAppointment, setStartAppointment] = useState(1);
  const [showStartModal, setShowStartModal] = useState(false);
  const [showEndModal, setShowEndModal] = useState(false);
  const handleCloseStartModal = () => {
    setShowStartModal(false);
  };

  const { videoCallData, isLoading } = useSelector((state) => state.vonageData);

  const moveToNext = () => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    }
    dispatch(joinAppointment(finalData))
  }

  const videoCallTokenData = () => {
    const finalData = {
      sessionId: slotDescription?.vonageSessionId,
    };
    dispatch(getVonageVideoCallToken({finalData, moveToNext}));
    setStartAppointment(2);
  };

  return (
    <>
      {startAppointment === 1 && (
        <div className="video-appointment">
          <div className="d-flex align-items-center justify-content-center h-100">
            <HiExclamation className="exclamation-icon me-2 mb-3" size={28} />
            <p className="not-joined-text m-0" style={{ fontWeight: 600 }}>
              The patient has not yet joined the appointment
            </p>
          </div>
          <div className="px-2 my-4 d-flex flex-wrap justify-content-end align-items-center">
          
              <Button
                style={{ background: "#000071" }}
                onClick={() => setShowStartModal(true)}
                className="px-4"
              >
                Start Appointment
              </Button>
            
          </div>
          <Modal
            show={showStartModal}
            onHide={handleCloseStartModal}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
            className="appointment-modal"
          >
            <Modal.Header closeButton></Modal.Header>
            <Modal.Body>
              <div className="d-flex justify-content-center">
                <span className="stethoscope-icon">
                  <FaStethoscope size={34} />
                </span>
              </div>
              <div className="my-3">
                <h4 className="text-center w-50 mx-auto my-0">
                  You are about to start this appointment
                </h4>
              </div>
              <span className="d-flex justify-content-center">
                <Button
                    className={`start-apt-btn ${isLoading && "disabled"}`}
                    onClick={() => videoCallTokenData()}
                  >
                    {isLoading === true ? (
                      <ClipLoader
                        color="white"
                        size={25}
                        className="d-flex m-auto"
                      />
                    ) : (
                      "Start"
                    )}
                  </Button>
              </span>
              <span
                className="d-flex justify-content-center align-items-center mb-3 mt-4"
                style={{ cursor: "pointer" }}
              >
                <IoIosArrowRoundBack size={25} />
                <p
                  className="mb-0"
                  style={{ fontWeight: 500, fontSize: "18px" }}
                >
                  Go Back
                </p>
              </span>
            </Modal.Body>
          </Modal>
        </div>
      )}

      {startAppointment === 2 && isLoading === false && (
        <div className="appointment-started">
          <span className="">
            <VideoCall videoCallData={videoCallData} user={user} setShowEndModal={setShowEndModal} slotDescription={slotDescription}/>
          </span>
        </div>
      )}
    </>
  );
}
