import React, { useEffect, useState } from "react";
import Publisher from "./Publisher";

import Subscriber from "./Subscriber";
import { OTSession, OTStreams, preloadScript } from "opentok-react";
import { HiExclamation } from "react-icons/hi";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.otSession = React.createRef();
    this.state = {
      error: null,
      connected: false,
    };
    this.sessionEvents = {
      sessionConnected: () => {
        this.setState({ connected: true });
      },
      sessionDisconnected: () => {
        this.setState({ connected: false });
      },
    };
  }

  onSignalRecieve = (signal) => {
    console.log("onSignalReceive => ", JSON.parse(signal.data));
    // based on signal data type you can do use switch or conditional statements
  };

  onError = (err) => {
    this.setState({ error: `Failed to connect: ${err.message}` });
  };

  render() {
    return (
      <OTSession
        ref={this.otSession}
        apiKey={`47602941`}
        sessionId={this.props?.videoCallData?.vonageSessionId}
        token={this.props?.videoCallData?.token}
        eventHandlers={this.sessionEvents}
        onError={this.onError}
      >
        {this.state.error ? <div id="error">{this.state.error}</div> : null}
        <div className="position-relative w-100">
          {/* <ConnectionStatus connected={this.state.connected} /> */}

          {this.otSession?.current?.state?.streams?.length === 0 && (
            <Abc
              show={this.otSession?.current?.state?.streams?.length}
              session={this.otSession}
            />
          )}
          <OTStreams>
            <Subscriber slotDescription={this.props.slotDescription}/>
          </OTStreams>
          <Publisher
            user={this.props.user}
            setShowEndModal={this.props.setShowEndModal}
            otSession={this.otSession}
            slotDescription={this.props.slotDescription}
            videoCallData={this.props?.videoCallData}
          />
        </div>
        {/* <ChatComponents
          session={this.otSession?.current?.sessionHelper.session}
        /> */}
      </OTSession>
    );
  }
}

export default preloadScript(App);

function Abc({ session }) {
  const [streamLength, setStreamLength] = useState();
    useEffect(() => {
    setStreamLength(session.current?.state?.streams?.length);
  }, [session.current?.state?.streams?.length]);
  return (
    <div
      className={`video-appointment abcd ${streamLength === 0 ? "d-block" : "d-none"}`}
    >
      <div className="d-flex align-items-center justify-content-center h-100">
        <HiExclamation className="exclamation-icon me-2 mb-3" size={28} />
        <p className="not-joined-text m-0" style={{ fontWeight: 600 }}>
          The Patient has not yet joined the appointment
        </p>
      </div>
    </div>
  );
}