import { Button, Modal } from "react-bootstrap";
import { FaStethoscope } from "react-icons/fa";

function AppointmentEndModel(props) {
  const { showEndModal, handleCloseEndModal, discounthandlerEnd } = props;

  function doctorAppointmentNotes() {
    discounthandlerEnd();
    handleCloseEndModal();
  }

  return (
    <div>
      <Modal
        show={showEndModal}
        onHide={handleCloseEndModal}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        className="appointment-modal"
      >
        <Modal.Header closeButton></Modal.Header>
        <Modal.Body>
          <div className="d-flex justify-content-center ">
            <span className="stethoscope-icon">
              <FaStethoscope size={34} />
            </span>
          </div>
          <div className="my-3">
            <h4
              className="text-center w-50 mx-auto my-0"
              style={{ fontWeight: 600 }}
            >
              You are about to end this appointment
            </h4>
            <p className="text-center mt-3 mb-4">
              Note: If you end tha appointment, you will be unable to reconnect
            </p>
          </div>
          <span className="d-flex justify-content-center mt-2 mb-5">
            <Button
              className="end-apt-btn"
              onClick={() => doctorAppointmentNotes()}
            >
              End Appointment
            </Button>
          </span>
        </Modal.Body>
      </Modal>
    </div>
  );
}

export default AppointmentEndModel;
