import React, { useState } from "react";
import {
  Accordion,
  Button,
  Card,
  Col,
  Form,
  Row,
  Tab,
  Tabs,
} from "react-bootstrap";
import VideoAppointment from "./VideoAppointment";
import GenericPatientCard from "../../../../components/Theme/GenericPatientCard";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { getPatientAppointmentDetail } from "../../../../app/features/appointment/appointment.slice";
import moment from "moment";
import { useForm } from "react-hook-form";
import { getPatientNotes } from "../../../../app/features/patientNotes/patientNotes.slice";
import {
  createPatientPharmacy,
  deletePatientPharmacy,
  getAllPatientPharmacy,
  getPatientPharmacyData,
} from "../../../../app/features/patientPharmacy/patientPharmacy.slice";
import AppointmentNotes from "./AppointmentNotes";
import { RiDeleteBin5Fill } from "react-icons/ri";
import { BsPencilSquare } from "react-icons/bs";
import { ConfirmationModal } from "../../../../components/Modal/ConfirmationModal";
import CallButton from "./CallButton";

export default function TeleConsultation() {
  const { register, handleSubmit, setValue, reset } = useForm();
  const { patientAppointedDetail } = useSelector((state) => state.appointment);
  const [deleteId, setDeleteId] = useState("");
  const { patientPharmacy } = useSelector((state) => state);
  const [editing, setEditing] = useState(false);
  const [showPharmacyId, setShowPharmacyId] = useState();
  const [selectedPharmacy, setSelectedPharmacy] = useState({});
  const [open, setOpen] = useState(false);
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));
  console.log("slotDescription", slotDescription);
  const dispatch = useDispatch();
  console.log("patientAppointedDetail", patientAppointedDetail);
  useEffect(() => {
    const finalData = {
      patientId: slotDescription?.patientId,
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientAppointmentDetail(finalData));
  }, [dispatch, slotDescription?.appointmentId, slotDescription?.patientId]);

  useEffect(() => {
    const finalData = {
      patientId: slotDescription?.patientId,
    };
    dispatch(getPatientPharmacyData(finalData));
  }, [dispatch, slotDescription?.patientId]);

  useEffect(() => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getPatientNotes(finalData));
  }, [dispatch, slotDescription?.appointmentId]);

  useEffect(() => {
    const finalData = {
      pharmacyName: null,
      pharmacyLocation: null,
    };
    dispatch(getAllPatientPharmacy(finalData));
  }, [dispatch]);

  const moveToNext = () => {
    const finalData = {
      PatientId: slotDescription?.patientId,
    };
    dispatch(getPatientPharmacyData(finalData));
    setEditing(false);
  };

  function createPharmacyData(submittedData) {
    const finalData = {
      pharmacyId: showPharmacyId ? showPharmacyId : 0,
      pharmacyEmail: submittedData?.pharmacyEmail,
      pharmacyName: submittedData?.pharmacyName,
      pharmacyLocation: submittedData?.pharmacyLocation,
      country: submittedData?.country,
      ig_xcord: "",
      ig_ycord: "",
      userId: patientAppointedDetail?.detailDTO?.patientId,
    };
    if (patientPharmacy?.patientPharmacyData?.pharmacyId) {
      dispatch(createPatientPharmacy({ finalData, moveToNext }));
    } else {
      dispatch(createPatientPharmacy({ finalData, moveToNext }));
    }
    const selectedPharmacyData = patientPharmacy?.allPatientPharmacyData?.find(
      (item) => item.pharmacyName === submittedData?.pharmacyName
    );

    if (selectedPharmacyData) {
      setValue("pharmacyLocation", selectedPharmacyData.pharmacyLocation);
      setValue("country", selectedPharmacyData.country);
      setValue("pharmacyEmail", selectedPharmacyData.pharmacyEmail);
      setSelectedPharmacy(selectedPharmacyData);
    }
  }
  const onCreateSuccess = () => {
    const finalData = {
      patientId: slotDescription?.patientId,
    };
    dispatch(getPatientPharmacyData(finalData));
    setEditing(false);
    reset();
  };
  function deletePharmacyData() {
    const finalData = {
      patientId: deleteId,
    };
    dispatch(deletePatientPharmacy({ finalData, onCreateSuccess }));
  }

  return (
    <div className="p-4 consultation-main vh-100">
      <Row>
        <Col xl={3} lg={4} md={4} xs={12} className="h-100 mb-3">
          <GenericPatientCard
            name={`${patientAppointedDetail?.detailDTO?.firstName} ${patientAppointedDetail?.detailDTO?.lastName}`}
            genderInfo="true"
            gender={patientAppointedDetail?.detailDTO?.gender}
            dobInfo="true"
            dob={moment(patientAppointedDetail?.detailDTO?.dob).format(
              "DD-MM-YYYY"
            )}
            // professionInfo="true"
            // profession="Business"
            currentLocationInfo="true"
            currentLocation={patientAppointedDetail?.detailDTO?.currentAddress}
            patientLocalGPInfo="true"
            patientLocalGP={patientAppointedDetail?.detailDTO?.patientLocalGP}
            emailInfo="true"
            email={patientAppointedDetail?.detailDTO?.email}
            phoneInfo="true"
            phone={patientAppointedDetail?.detailDTO?.phoneNumber}
            nextOfKinNameInfo="true"
            nextOfKinName={patientAppointedDetail?.detailDTO?.nextOfKinName}
            nextOfKinNumberInfo="true"
            nextOfKinNumber={patientAppointedDetail?.detailDTO?.nextOfKinNumber}
            image={patientAppointedDetail?.detailDTO?.imageUrl}
          />
        </Col>
        <Col xl={6} lg={8} md={8} xs={12} className="mb-3 bg-white px-0">
          <Card className="tele-consultation-tabs">
            <Card.Body className="p-0">
              <Tabs
                defaultActiveKey="videoAppointment"
                id="uncontrolled-tab-example"
              >
                <Tab eventKey="videoAppointment" title="Appointment">
                  <VideoAppointment slotDescription={slotDescription} />
                </Tab>
                {/* <Tab eventKey="audioAppointment" title="Audio Appointment">
                  <p>Audio Appointment</p>
                </Tab> */}
              </Tabs>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={3} lg={6} md={12} xs={12} className="mb-3">
          <Card className="tele-consultation-accordion h-100">
            <Card.Body className="mx-3">
              <Accordion defaultActiveKey="0">
                <Accordion.Item eventKey="0" className="my-3">
                  <Accordion.Header>
                    <h5 className="mb-0">Past Medical History</h5>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {patientAppointedDetail?.patientHistory?.pastMedicalHistory
                      ? patientAppointedDetail?.patientHistory
                          ?.pastMedicalHistory
                      : "N/A"}
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1" className="my-3">
                  <Accordion.Header>
                    <h5 className="mb-0">Current Medications</h5>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {patientAppointedDetail?.patientHistory?.currentMedical
                      ? patientAppointedDetail?.patientHistory?.currentMedical
                      : "N/A"}
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="2" className="my-3">
                  <Accordion.Header>
                    <h5 className="mb-0">Allergy (with any medication)</h5>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {patientAppointedDetail?.patientHistory?.allergyHistory
                      ? patientAppointedDetail?.patientHistory?.allergyHistory
                      : "N/A"}
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="3" className="my-3">
                  <Accordion.Header>
                    <h5 className="mb-0">Pharmacy</h5>
                  </Accordion.Header>
                  <Accordion.Body className="pt-2">
                    <Form onSubmit={handleSubmit(createPharmacyData)}>
                      <Row>
                        <Col lg={12}>
                          <div className="d-flex justify-content-between align-items-baseline">
                            <span className="w-100">
                              <Form.Label className="fw-bold">
                                Pharmacy Name
                              </Form.Label>
                              <Form.Group>
                                {patientPharmacy?.patientPharmacyData
                                  ?.pharmacyName && editing === false ? (
                                  <p>
                                    {
                                      patientPharmacy?.patientPharmacyData
                                        ?.pharmacyName
                                    }
                                  </p>
                                ) : (
                                  <Form.Control
                                    list="pharmacyNameList"
                                    defaultValue={
                                      patientPharmacy?.patientPharmacyData
                                        ?.pharmacyName ?? ""
                                    }
                                    {...register("pharmacyName")}
                                    onChange={(e) => {
                                      // setShowPharmacyId(index)
                                      const selectedPharmacyData =
                                        patientPharmacy?.allPatientPharmacyData?.find(
                                          (item) =>
                                            item.pharmacyName === e.target.value
                                        );

                                      if (selectedPharmacyData) {
                                        setValue(
                                          "pharmacyLocation",
                                          selectedPharmacyData.pharmacyLocation
                                        );
                                        setValue(
                                          "country",
                                          selectedPharmacyData.country
                                        );
                                        setSelectedPharmacy(
                                          selectedPharmacyData
                                        );
                                        setShowPharmacyId(
                                          selectedPharmacyData.pharmacyId
                                        );
                                      } else {
                                        setValue("pharmacyLocation", "");
                                        setValue("country", "");
                                        setSelectedPharmacy({});
                                      }
                                    }}
                                    placeholder="Select Pharmacy Name"
                                    size="lg"
                                    className={`${
                                      !patientPharmacy?.allPatientPharmacyData &&
                                      "bg-transparent"
                                    }`}
                                    readOnly={
                                      patientPharmacy?.patientPharmacyData
                                        ?.pharmacyName && editing
                                        ? false
                                        : patientPharmacy?.patientPharmacyData
                                            ?.pharmacyName
                                        ? true
                                        : false
                                    }
                                    disabled={
                                      !patientPharmacy?.allPatientPharmacyData
                                    }
                                  />
                                )}
                                <datalist id="pharmacyNameList">
                                  {patientPharmacy?.allPatientPharmacyData?.map(
                                    (item) => (
                                      <option
                                        key={item?.pharmacyId}
                                        value={item?.pharmacyName}
                                      >
                                        {item.pharmacyName}
                                      </option>
                                    )
                                  )}
                                </datalist>
                              </Form.Group>
                            </span>
                            {patientPharmacy?.patientPharmacyData?.pharmacyId &&
                              patientPharmacy?.patientPharmacyData
                                ?.pharmacyEmail &&
                              editing === false && (
                                <div>
                                  <div
                                    className="d-flex justify-content-center align-items-center mb-2"
                                    style={{
                                      height: "29px",
                                      width: "32px",
                                      backgroundColor: "#E1EBFF",
                                      borderRadius: "5px",
                                      cursor: "pointer",
                                    }}
                                    onClick={() => setEditing(true)}
                                  >
                                    <BsPencilSquare
                                      style={{
                                        color: "#2269F2",
                                        fontSize: "18px",
                                      }}
                                    />
                                  </div>
                                  <div
                                    className="d-flex justify-content-center align-items-center"
                                    style={{
                                      height: "29px",
                                      width: "32px",
                                      backgroundColor: "#FFDADD",
                                      borderRadius: "5px",
                                      cursor: "pointer",
                                    }}
                                    onClick={() => {
                                      setDeleteId(slotDescription?.patientId);
                                      setOpen(true);
                                    }}
                                  >
                                    <RiDeleteBin5Fill
                                      style={{
                                        color: "#E63946",
                                        fontSize: "18px",
                                      }}
                                    />
                                  </div>
                                </div>
                              )}
                          </div>
                          <Form.Group>
                            <Form.Label className="fw-bold mt-2">
                              Address
                            </Form.Label>
                            {patientPharmacy?.patientPharmacyData
                              ?.pharmacyLocation && editing === false ? (
                              <p>
                                {
                                  patientPharmacy?.patientPharmacyData
                                    ?.pharmacyLocation
                                }
                              </p>
                            ) : (
                              <Form.Control
                                type="text"
                                size="lg"
                                placeholder="Select Address"
                                {...register("pharmacyLocation")}
                                defaultValue={
                                  patientPharmacy?.patientPharmacyData
                                    ?.pharmacyLocation
                                    ? patientPharmacy?.patientPharmacyData
                                        ?.pharmacyLocation
                                    : ""
                                }
                                className={`${
                                  !patientPharmacy?.allPatientPharmacyData &&
                                  "bg-transparent"
                                }`}
                                readOnly={
                                  patientPharmacy?.patientPharmacyData
                                    ?.pharmacyLocation && editing
                                    ? false
                                    : patientPharmacy?.patientPharmacyData
                                        ?.pharmacyLocation
                                    ? true
                                    : false
                                }
                                maxLength={250}
                                disabled={
                                  !patientPharmacy?.allPatientPharmacyData
                                }
                              />
                            )}
                          </Form.Group>
                          <Form.Group>
                            <Form.Label className="fw-bold mt-2">
                              Country
                            </Form.Label>
                            {patientPharmacy?.patientPharmacyData?.country &&
                            editing === false ? (
                              <p>
                                {patientPharmacy?.patientPharmacyData?.country}
                              </p>
                            ) : (
                              <Form.Control
                                type="text"
                                size="lg"
                                placeholder="Country"
                                {...register("country")}
                                defaultValue={
                                  patientPharmacy?.patientPharmacyData
                                    ?.country ?? ""
                                }
                                readOnly={
                                  patientPharmacy?.patientPharmacyData
                                    ?.country && editing
                                    ? false
                                    : patientPharmacy?.patientPharmacyData
                                        ?.country
                                    ? true
                                    : false
                                }
                                className={`${
                                  !patientPharmacy?.allPatientPharmacyData &&
                                  "bg-transparent"
                                }`}
                                maxLength={250}
                                disabled={
                                  !patientPharmacy?.allPatientPharmacyData
                                }
                              />
                            )}
                          </Form.Group>

                          <Form.Group>
                            <Form.Label className="fw-bold mt-2">
                              Pharmacy Email
                            </Form.Label>
                            {patientPharmacy?.patientPharmacyData
                              ?.pharmacyEmail && editing === false ? (
                              <p>
                                {
                                  patientPharmacy?.patientPharmacyData
                                    ?.pharmacyEmail
                                }
                              </p>
                            ) : (
                              <Form.Control
                                type="email"
                                size="lg"
                                placeholder="Pharmacy Email"
                                {...register("pharmacyEmail")}
                                defaultValue={
                                  patientPharmacy?.patientPharmacyData
                                    ?.pharmacyEmail ?? ""
                                }
                                readOnly={
                                  patientPharmacy?.patientPharmacyData
                                    ?.pharmacyEmail && editing
                                    ? false
                                    : patientPharmacy?.patientPharmacyData
                                        ?.pharmacyEmail
                                    ? true
                                    : false
                                }
                                className={`${
                                  !patientPharmacy?.allPatientPharmacyData &&
                                  "bg-transparent"
                                }`}
                                maxLength={250}
                                disabled={
                                  !patientPharmacy?.allPatientPharmacyData
                                }
                              />
                            )}
                          </Form.Group>

                          {(!patientPharmacy?.patientPharmacyData?.pharmacyId ||
                            !patientPharmacy?.patientPharmacyData
                              ?.pharmacyEmail ||
                            editing === true) && (
                            <Button
                              className="px-3 mt-3 primary_bg float-right"
                              size="sm"
                              type="submit"
                            >
                              {patientPharmacy?.patientPharmacyData?.pharmacyId
                                ? "Update "
                                : "Add "}
                              Pharmacy
                            </Button>
                          )}
                        </Col>
                      </Row>
                    </Form>
                    <ConfirmationModal
                      show={open}
                      onHide={() => setOpen(false)}
                      heading={"Delete Pharmacy"}
                      title={"this pharmacy"}
                      removeFunc={deletePharmacyData}
                    />
                  </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="4" className="my-3">
                  <Accordion.Header>
                    <h5 className="mb-0">Appointment Reason</h5>
                  </Accordion.Header>
                  <Accordion.Body className="py-4">
                    {slotDescription?.reasonForAppoinment
                      ? slotDescription?.reasonForAppoinment
                      : "N/A"}
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
            </Card.Body>
          </Card>
        </Col>
        <Col xl={3} lg={6} md={12} xs={12} className="mb-3">
          <Card>
            <Card.Body className="mx-3">
              <CallButton />
            </Card.Body>
          </Card>
        </Col>
        <Col xl={6} lg={12} md={12} xs={12}>
          <AppointmentNotes />
        </Col>
      </Row>
    </div>
  );
}
