import React from "react";
import { OTPublisher } from "opentok-react";
import { Button } from "react-bootstrap";
import { AiFillAudio, AiOutlineAudioMuted } from "react-icons/ai";
import { FaVideo, FaVideoSlash } from "react-icons/fa";
import AppointmentEndModel from "./AppointmentEndModel";

class Publisher extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      error: null,
      audio: true,
      video: this.props.videoCallData?.isAudio
        ? false
        : this.props.videoCallData?.isVideo
        ? true
        : true,
      showEndModal: false,
      videoSource: "camera",
    };
  }
  setAudio = (audio) => {
    this.setState({ audio });
  };

  setVideo = (video) => {
    this.setState({ video });
  };

  setShowEndModal = (showEndModal) => {
    this.setState({ showEndModal });
  };

  changeVideoSource = (videoSource) => {
    this.state.videoSource !== "camera"
      ? this.setState({ videoSource: "camera" })
      : this.setState({ videoSource: "screen" });
  };

  onError = (err) => {
    this.setState({ error: `Failed to publish: ${err.message}` });
  };
  discounthandler = async () => {
    await this.props?.otSession?.current?.sessionHelper?.session?.destroy();
    // history(PATH.MYAPPOINTMENTS);
  };
  discounthandlerEnd = async () => {
    await this.props.otSession.current.sessionHelper.session.destroy();
  };
  render() {
    return (
      <div className="videocall publisher">
        {/* {this.state.error ? <div id="error">{this.state.error}</div> : null} */}
        <div className="d-flex justify-content-end me-3 position-relative publisher-video-box">
          <div>
            <OTPublisher
              properties={{
                publishAudio: this.state.audio,
                publishVideo: this.state.video,
                videoSource:
                  this.state.videoSource === "screen" ? "screen" : undefined,
              }}
              // onError={this.onError}
            />
          </div>
          <div
            className="position-absolute d-flex justify-content-between align-items-center"
            style={{ right: "1rem", top: "8.8rem", width: "25%" }}
          >
            <div className="publisher-name">
              <h6 className="text-white mb-0">{this.props.user.name}</h6>
            </div>
            {this.state.audio === false && (
              <div className="publisher-mute">
                <AiOutlineAudioMuted
                  className="strem--controls-icon cursor m-2 text-white"
                  size={20}
                  onClick={() => this.setAudio(false)}
                />
              </div>
            )}
          </div>
        </div>
        {/* <CheckBox
                    label="Share Screen"
                    onChange={this.changeVideoSource}
                /> */}

        {/* <CheckBox
                    label="Publish Audio"
                    initialChecked={this.state.audio}
                    onChange={this.setAudio}
                />

                <CheckBox
                    label="Publish Video"
                    initialChecked={this.state.video}
                    onChange={this.setVideo}
                /> */}
        <div className="row pt-5 px-2">
          <div className="randox-div">
            <div className="position_set">
              <div className="d-flex justify-content-between streem-control-container w-100">
                <div>
                  {this.state.audio ? (
                    <span className="botton-icons me-4">
                      <AiFillAudio
                        className="strem--controls-icon cursor m-2"
                        size={24}
                        onClick={() => this.setAudio(false)}
                      />
                    </span>
                  ) : (
                    <span className="botton-icons me-4">
                      <AiOutlineAudioMuted
                        className="strem--controls-icon cursor m-2"
                        size={24}
                        onClick={() => this.setAudio(true)}
                      />
                    </span>
                  )}
                  {this.state.video ? (
                    <span className="botton-icons me-4">
                      <FaVideo
                        className="strem--controls-icon cursor m-2"
                        size={24}
                        onClick={() => this.setVideo(false)}
                      />
                    </span>
                  ) : (
                    <span className="botton-icons me-4">
                      <FaVideoSlash
                        className="strem--controls-icon cursor m-2"
                        size={24}
                        onClick={() => this.setVideo(true)}
                      />
                    </span>
                  )}
                </div>
                <span className="appointment-last-span">
                  {/* {this.props.user &&
                    this.props.user &&
                    this.props.user.token &&
                    this.props.user.role === "Doctor" && (
                      <div
                        className="end_call"
                        // onClick={async () => {
                        //   this.discounthandlerEnd();
                        // }}
                      >
                        <Button
                          style={{ background: "#FD2121", border: "none" }}
                          onClick={() => this.setShowEndModal(true)}
                          className="px-4"
                        >
                          End Appointment
                        </Button>
                      </div>
                    )} */}
                  {this.props.user &&
                    this.props.user &&
                    this.props.user.token &&
                    this.props.user.role === "Patient" && (
                      <div
                        className="end_call"
                        // onClick={async () => {
                        //   this.discounthandler();
                        // }}
                      >
                        <Button
                          style={{ background: "#FD2121", border: "none" }}
                          onClick={() => this.setShowEndModal(true)}
                          className="px-4"
                        >
                          End Appointment
                        </Button>
                      </div>
                    )}
                </span>
              </div>
            </div>
          </div>
        </div>
        <AppointmentEndModel
          showEndModal={this.state.showEndModal}
          handleCloseEndModal={() => this.setShowEndModal(false)}
          discounthandlerEnd={async () => {
            this.discounthandlerEnd();
          }}
          slotDescription={this.props.slotDescription}
        />
      </div>
    );
  }
}
export default Publisher;
